<template>
  <div>
    <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li v-for="(item, index) in goods" class="menu-item" :class="{'current': currentIndex === index}" @click="selectMenu(index, $event)">
            <span class="text border-1px">
              <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name }}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodWrapper">
        <ul>
          <li v-for="item in goods" class="food-list food-list-hook">
            <h1 class="title">{{ item.name }}</h1>
            <ul>
              <li @click="selectFood(food, $event)" v-for="food in item.foods" class="food-item border-1px">
                <div class="icon">
                  <img width="57" height="57" :src="food.icon">
                </div>
                <div class="content">
                  <h2 class="name">{{ food.name }}</h2>
                  <p class="desc">{{ food.description }}</p>
                  <div class="extra">
                    <span class="count">月售{{ food.sellCount }}份</span><span>好评率{{ food.rating }}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{ food.price }}</span><span class="old" v-show="food.oldPrice">￥{{ food.oldPrice }}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <shopcart ref="shopcart" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" :select-foods="selectFoods"></shopcart>
    </div>
    <keep-alive>
      <food :food="selectedFood" ref="food"></food>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import shopcart from '@/views/shopcart/shopcart'
  import food from '@/views/food/food'
  import cartcontrol from '@/components/cartcontrol/cartcontrol'
  import Bus from '@/public/js/eventBus'

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        goods: [],
        classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      }
    },
    components: {
      shopcart,
      cartcontrol,
      food
    },
    computed: {
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i]
          let height2 = this.listHeight[i + 1]
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i
          }
        }
        return 0
      },
      selectFoods () {
        let foods = []
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food)
            }
          })
        })
        return foods
      }
    },
    methods: {
      selectFood (food, event) {
        if (!event._constructed) {
          return
        }
        this.selectedFood = food
        this.$refs.food.show()
      },
      // 子组件事件触发
      drop (target) {
        // 体验优化，异步执行下落动画
        this.$nextTick(() => {
          // 父组件传递给子组件
          this.$refs.shopcart.drop(target)
        })
      },
      getGoodsData () {
        this.$axios.get('https://easy-mock.com/mock/5a3b1e9bab7fde5ac993b2de/elemeSellAppData/sellerData').then((res) => {
          if (res.data) {
            this.goods = res.data.goods
            this.$nextTick(() => {
              this._initScroll()
              this._calculateHeight()
            })
            console.log('数据读取成功')
          }
        }).catch((err) => {
          console.log('数据读取失败' + err)
        })
      },
      _initScroll () {
        this.menuScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        })
        this.foodsScroll = new BScroll(this.$refs.foodWrapper, {
          click: true,
          probeType: 3
        })
        this.foodsScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y))
        })
      },
      _calculateHeight () {
        // 获取food-list
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
      },
      selectMenu (index, event) {
        if (!event._constructed) {
          return
        }
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
        let el = foodList[index]
        this.foodsScroll.scrollToElement(el, 300)
      }
    },
    mounted () {
      this.getGoodsData()
      Bus.$on('cart.add', el => {
        this.$nextTick(() => {
          // 这里this.$refs.shpcart指代shopcart这个DOM节点（导入并使用的组件中添加ref="shopcart"）
          this.$refs.shopcart.drop(el)
        })
      })
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "../../public/sass/mixin.scss";
  .goods {
    display: flex;
    overflow: hidden;
    position: absolute;
    top: 177px;
    bottom: 46px;
    width: 100%;
    .menu-wrapper {
      flex: 0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .menu-item {
        display: table;
        padding: 0 12px;
        width: 56px;
        height: 54px;
        line-height: 14px;
        &.current {
          z-index: 10;
          position: relative;
          margin-top: -1px;
          font-size: 700;
          background: #ffffff;
          .text {
            @include border-none;
          }
        }
        .icon {
          display: inline-block;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px 12px;
          background-repeat: no-repeat;
          vertical-align: top;
          &.decrease {
            @include bg-image('decrease_3')
          }
          &.discount {
            @include bg-image('discount_3')
          }
          &.guarantee {
            @include bg-image('guarantee_3')
          }
          &.invoice {
            @include bg-image('invoice_3')
          }
          &.special {
            @include bg-image('special_3')
          }
        }
        .text {
          display: table-cell;
          width: 56px;
          font-size: 12px;
          vertical-align: middle;
          @include border-1px(rgba(7, 17, 27, .1));
        }
      }
    }
    .foods-wrapper {
      flex: 1;
      .title {
        padding-left: 14px;
        height: 26px;
        font-size: 12px;
        line-height: 26px;
        color: rgb(147, 153, 159);
        background: #f3f5f7;
        border-left: 2px solid #d9dde1;
      }
      .food-item {
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
        @include border-1px(rgba(7, 17, 27, .1));
        &:last-child {
          margin-bottom: 0;
          @include border-none;
        }
        .icon {
          flex: 0 0 57px;
          margin-right: 10px;
          img {
            border-radius: 2px;
          }
        }
        .content {
          flex: 1;
          .name {
            margin-top: 2px;
            font-size: 14px;
            line-height: 14px;
            color: rgb(7, 17, 27);
          }
          .desc,
          .extra {
            font-size: 10px;
            line-height: 10px;
            color: rgb(147, 154, 159);
          }
          .desc {
            margin: 8px 0;
            line-height: 12px;
          }
          .extra {
            .count {
              margin-right: 12px;
            }
          }
          .price {
            font-weight: 700;
            line-height: 24px;
            .now {
              margin-right: 8px;
              font-size: 14px;
              color: rgb(240, 20, 20);
            }
            .old {
              text-decoration: line-through;
              font-size: 10px;
              color: rgb(7, 17, 27);
            }
          }
          .cartcontrol-wrapper {
            position: absolute;
            right: 0;
            bottom: 12px;
          }
        }
      }
    }
  }

</style>



