<template>
  <div>
    <div class="ratings" ref="ratings">
      <div class="ratings-content">
        <div class="overview">
          <div class="overview-left">
            <h1 class="score">{{ seller.score }}</h1>
            <div class="title">综合评分</div>
            <div class="rank">高于周边商家{{ seller.rankRate }}%</div>
          </div>
          <div class="overview-right">
            <div class="score-wrapper">
              <span class="title">服务态度</span>
              <star :size="36" :score="seller.serviceScore"></star>
              <span class="score">{{ seller.serviceScore }}</span>
            </div>
            <div class="score-wrapper">
              <span class="title">商品评分</span>
              <star :size="36" :score="seller.foodScore"></star>
              <span class="score">{{ seller.foodScore }}</span>
            </div>
            <div class="delivery-wrapper">
              <span class="title">送达时间</span>
              <span class="delivery">{{ seller.deliveryTime }}分钟</span>
            </div>
          </div>
        </div>
        <split></split>
        <ratingselect :select-type="selectType" :only-content="onlyContent" :ratings="ratings"></ratingselect>
        <div class="rating-wrapper">
          <ul>
            <li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType, rating.text)">
              <div class="avatar">
                <img :src="rating.avatar" width="28" height="28">
              </div>
              <div class="content">
                <h1 class="name">{{ rating.username }}</h1>
                <div class="star-wrapper">
                  <star :size="24" :score="rating.score"></star>
                  <span class="delivery" v-show="rating.deliveryTime">{{ rating.deliveryTime }}分钟送达</span>
                </div>
                <p class="text">{{ rating.text }}</p>
                <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                  <span class="icon-thumb_up"></span>
                  <span v-for="item in rating.recommend" class="item">{{ item }}</span>
                </div>
                <div class="time">
                  {{ rating.rateTime | formatDate }}
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import BScroll from 'better-scroll'
import star from '@/components/star/star'
import split from '@/components/split/split'
import ratingselect from '@/components/ratingselect/ratingselect'
import { formatDate } from '@/public/js/date'
import Bus from '@/public/js/eventBus'

const ALL = 2

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      ratings: [],
      selectType: ALL,
      onlyContent: false
    }
  },
  methods: {
    getRatingsData () {
      this.$axios.get('https://easy-mock.com/mock/5a3b1e9bab7fde5ac993b2de/elemeSellAppData/sellerData').then((res) => {
        if (res.data) {
          this.ratings = res.data.ratings
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratings, {
              click: true
            })
          })
          console.log('数据读取成功')
        }
      }).catch((err) => {
        console.log('数据读取失败' + err)
      })
    },
    needShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL) {
        return true
      } else {
        return type === this.selectType
      }
    }
  },
  filters: {
    formatDate (time) {
      let date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm')
    }
  },
  components: {
    // shopcart
    star,
    split,
    ratingselect
  },
  mounted () {
    this.getRatingsData()
    Bus.$on('ratingType.select', (selectType) => {
      this.selectType = selectType
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    })
    Bus.$on('content.toggle', (onlyContent) => {
      this.onlyContent = onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/scss" lang="scss" scoped>
  @import "../../public/sass/mixin.scss";

  .ratings {
    overflow: hidden;
    position: absolute;
    top: 176px;
    bottom: 0;
    left: 0;
    width: 100%;
    .overview {
      display: flex;
      padding: 18px 0;
      .overview-left {
        flex: 0 0 137px;
        padding: 6px 0;
        width: 137px;
        text-align: center;
        border-right: 1px solid rgba(7, 17, 27, .1);
        @media only screen and (max-width: 320px) {
          flex: 0 0 120px;
          width: 120px;
        }
        .score {
          font-size: 24px;
          line-height: 28px;
          color: rgb(255, 153, 0);
        }
        .title {
          margin-top: 6px;
          font-size: 12px;
          line-height: 12px;
          color: rgb(7, 17, 27);
        }
        .rank {
          margin-top: 8px;
          font-size: 10px;
          line-height: 10px;
          color: rgb(143, 153, 159);
        }
      }
      .overview-right {
        flex: 1;
        padding: 6px 0 6px 24px;
        @media only screen and (max-width: 320px) {
          padding-left: 6px;
        }
        .score-wrapper {
          margin-bottom: 8px;
          font-size: 0;
          .title {
            font-size: 12px;
            line-height: 18px;
            color: rgb(7, 17, 27);
          }
          .star {
            display: inline-block;
            margin: 0 12px;
            vertical-align: top;
          }
          .score {
            display: inline-block;
            font-size: 12px;
            line-height: 18px;
            color: rgb(255, 153, 0);
          }
        }
        .delivery-wrapper {
          font-size: 0;
          .title {
            font-size: 12px;
            line-height: 18px;
            color: rgb(7, 17, 27);
          }
          .delivery {
            margin-left: 12px;
            font-size: 12px;
            line-height: 18px;
            color: rgb(143, 153, 159);
          }
        }
      }
    }
    .rating-wrapper {
      padding: 0 18px;
      .rating-item {
        display: flex;
        padding: 18px 0;
        @include border-1px(rgba(7, 17, 27, .1));
        .avatar {
          flex: 0 0 28px;
          margin-right: 12px;
          width: 28px;
          img {
            border-radius: 50%;
          }
        }
        .content {
          flex: 1;
          position: relative;
          .name {
            margin-bottom: 4px;
            font-size: 10px;
            line-height: 12px;
            color: rgb(7, 17, 27);
          }
          .star-wrapper {
            margin-bottom: 6px;
            margin-right: 6px;
            font-size: 0;
            .star {
              display: inline-block;
              margin-right: 6px;
              vertical-align: top;
            }
            .delivery {
              display: inline-block;
              font-size: 10px;
              font-weight: 200;
              line-height: 12px;
              vertical-align: top;
              color: rgb(143, 153, 159);
            }
          }
          .text {
            margin-bottom: 8px;
            font-size: 12px;
            line-height: 18px;
            color: rgb(7, 17, 27);
          }
          .recommend {
            font-size: 0;
            line-height: 16px;
            .icon-thumb_up,
            .item {
              display: inline-block;
              margin: 0 8px 4px 0;
              font-size: 9px;
            }
            .icon-thumb_up {
              color: rgb(0, 160, 220);
            }
            .item {
              padding: 0 6px;
              color: rgb(147, 153, 159);
              border: 1px solid rgba(7, 17, 27, .1);
              border-radius: 1px;
            }
          }
          .time {
            position: absolute;
            top: 0;
            right: 0;
            font-size: 10px;
            font-weight: 200;
            line-height: 12px;
            color: rgb(143, 153, 159)
;          }
        }
      }
    }
  }
</style>



