<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{ seller.name }}</h1>
        <div class="desc">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{ seller.ratingCount }})</span>
          <span class="text">月售{{ seller.sellCount }}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{ seller.minPrice }}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryPrice }}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryTime }}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{'active': favorite}"></span>
          <span class="text">{{ favoriteText }}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper">
          <p class="content">{{ seller.bulletin }}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item" v-for="(item, index) in seller.supports">
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{ seller.supports[index].description }}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title">商家信息</h1>
        <ul>
          <li class="info-item" v-for="info in seller.infos">{{ info }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import star from '@/components/star/star'
import split from '@/components/split/split'
import {saveToLocal, loadFromLocal} from '@/public/js/store'

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  components: {
    star,
    split
  },
  watch: {
    'seller' () {
      this._initScroll()
      this._initPics()
    }
  },
  methods: {
    toggleFavorite (event) {
      if (!event._constructed) {
        return
      }
      this.favorite = !this.favorite
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    },
    // 页面整体滚动
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    // 商家实景横向滚动
    _initPics () {
      if (this.seller.pics) {
        let picWidth = 120
        let margin = 6
        let width = (picWidth + margin) * this.seller.pics.length - margin
        this.$refs.picList.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            })
          } else {
            this.picScroll.refresh()
          }
        })
      }
    }
  },
  computed: {
    favoriteText () {
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  mounted () {
    this._initScroll()
    this._initPics()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/scss" lang="scss" scoped>
  @import "../../public/sass/mixin.scss";

  .seller {
    overflow: hidden;
    position: absolute;
    top: 176px;
    bottom: 0;
    left: 0;
    width: 100%;
    .overview {
      position: relative;
      padding: 18px;
      .title {
        margin-bottom: 8px;
        font-size: 14px;
        line-height: 14px;
        color: (7, 17, 27);
      }
      .desc {
        padding-bottom: 18px;
        font-size: 0;
        line-height: 18px;
        @include border-1px(rgba(7, 17, 27, .1));
        .star {
          display: inline-block;
          margin-right: 8px;
          vertical-align: top;
        }
        .text {
          display: inline-block;
          margin-right: 12px;
          font-size: 10px;
          line-height: 18px;
          vertical-align: top;
          color: rgb(77, 85, 93);
        }
      }
      .remark {
        display: flex;
        padding-top: 18px;
        .block {
          flex: 1;
          text-align: center;
          border-right: 1px solid rgba(7, 17, 27, .1);
          &:last-of-type {
            border: none;
          }
          h2 {
            margin-bottom: 4px;
            font-size: 10px;
            line-height: 10px;
            color: rgb(147, 153, 159);
          }
          .content {
            line-height: 24px;
            font-size: 10px;
            color: rgb(7, 17, 27);
            .stress {
              font-size: 24px;
            }
          }

        }
      }
      .favorite {
        position: absolute;
        top: 18px;
        right: 11px;
        width: 50px;
        text-align: center;
        .icon-favorite {
          display: block;
          margin-bottom: 4px;
          font-size: 24px;
          line-height: 24px;
          color: #d4d6d9;
          &.active {
            color: rgb(240, 20, 20);
          }
          .text {
            font-size: 10px;
            line-height: 10px;
            color: rgb(77, 85, 93);
          }
        }
      }
    }
    .bulletin {
      padding: 18px 18px 0 18px;
      .title {
        margin-bottom: 8px;
        font-size: 14px;
        line-height: 14px;
        color: (7, 17, 27);
      }
      .content-wrapper {
        padding: 0 12px 16px 12px;
        @include border-1px(rgba(7, 17, 27, .1));
        .content {
          font-size: 12px;
          font-weight: 200;
          line-height: 24px;
          color: rgb(240, 20, 20);
        }
      }
      .supports{
        .support-item {
          padding: 16px 12px;
          font-size: 0;
          @include border-1px(rgba(7, 17, 27, .1));
          &:last-of-type {
            @include border-none;
          }
          .icon {
            display: inline-block;
            margin-right: 6px;
            width: 16px;
            height: 16px;
            vertical-align: top;
            background-size: 16px 16px;
            background-repeat: no-repeat;
            &.decrease {
              @include bg-image('decrease_4')
            }
            &.discount {
              @include bg-image('discount_4')
            }
            &.guarantee {
              @include bg-image('guarantee_4')
            }
            &.invoice {
              @include bg-image('invoice_4')
            }
            &.special {
              @include bg-image('special_4')
            }
          }
          .text {
            font-size: 12px;
            font-weight: 200;
            line-height: 16px;
            color: rgb(7, 17, 27);
          }
        }
      }
    }
    .pics {
      padding: 18px;
      .title {
        margin-bottom: 12px;
        font-size: 14px;
        line-height: 14px;
        color: rgb(7, 17, 27);
      }
      .pic-wrapper {
        overflow: hidden;
        width: 100%;
        white-space: nowrap;
        .pic-list {
          font-size: 0;
          .pic-item {
            display: inline-block;
            margin-right: 6px;
            width: 120px;
            height: 90px;
            img {
              border-radius: 2px;
            }
            &:last-of-type {
              margin: 0;
            }
          }
        }
      }
    }
    .info {
      padding: 18px 18px 0 18px;
      color: rgb(7, 17, 27);
      .title {
        padding-bottom: 12px;
        font-size: 14px;
        line-height: 14px;
        @include border-1px(rgba(7, 17, 27, .1));
      }
      .info-item {
        padding: 16px 12px;
        font-size: 12px;
        font-weight: 200;
        line-height: 16px;
        @include border-1px(rgba(7, 17, 27, .1));
        &:last-of-type {
          @include border-none;
        }
      }
    }
  }
</style>



